.cadastro {
  width: 100%;
  height: auto;
  background-color: #29abe2;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cadastro_body_titulo {
  margin-top: 20px;
  font-family: "Roboto Flex", sans-serif;
  font-weight: 500;
  color: rgb(255, 255, 255);
  font-size: 55px;
  text-align: center;
  margin-bottom: 60px;
}
.cadastro_body {
  width: 50%;
  height: 100%;
}
.cadastro_body_form {
  width: 100%;
  height: 60%;
}

.cadastro_body_form_btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.cadastro_body_form_button {
  font-size: 34px;
  padding-bottom: 7px;
  padding-left: 20px;
  padding-right: 20px;
  color: #29abe2;
  background-color: #012d51;
  font-family: "Roboto Flex", sans-serif;
  align-items: center;
  cursor: pointer;
}

/* styles input */
.cadastro_body_form_input {
  display: flex;
  flex-direction: column;
}
.cadastro_body_form_input label {
  color: rgb(255, 255, 255);
  font-family: Helvetica;
  font-size: 13px;
}
.cadastro_body_form_input input {
  margin-bottom: 20px;
  font-family: Helvetica;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  border-bottom: 2px solid rgb(214, 214, 214);
  padding: 8px;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: 500;
}
.cadastro_body_form_input input:focus {
  box-shadow: 0 0 0 0;
  outline: 0;
}
/* Estilos para telas pequenas (375px - 767px) */
@media screen and (min-width: 375px) and (max-width: 767px) {
.cadastro_body_titulo {
  font-size: 1.2em;
  margin-bottom: 2%;
}

.cadastro_body_form_input label,
.cadastro_body_form_input input {
  font-size: 0.8em;
}

.cadastro_body_form_button {
  font-size: 1em;
  width: 100%;
}
}

/* Estilos para telas médias (768px - 1023px) */
@media screen and (min-width: 768px) and (max-width: 1023px) {
.cadastro_body_titulo {
  font-size: 1.3em;
  margin-bottom: 3%;
}

.cadastro_body_form_input label,
.cadastro_body_form_input input {
  font-size: 0.9em;
}

.cadastro_body_form_button {
  font-size: 1.1em;
}
}

/* Estilos para telas grandes (1024px e acima) */
@media screen and (min-width: 1024px) {
.cadastro_body_titulo {
  font-size: 1.5em;
  margin-bottom: 4%;
}

.cadastro_body {
  width: 90%;
}

.cadastro_body_form_input label,
.cadastro_body_form_input input {
  font-size: 1em;
}

.cadastro_body_form_button {
  font-size: 1.2em;
  width: 100%;
}
}